import React from 'react';
import classnames from 'classnames';
import {Box, Spinner,Typography} from '@lapilli-ui/components';
import {useStoreSearch} from '@ywcas/base-context/hooks';

import {ProductResults} from '../../../../base/components';
import {useSearchContext} from "../../context";

const Block = (props) => {
    const {className, showImage, showCategories, imageSize, maxResultsToShow, layout, noResults} = props;

    const {query} = useSearchContext();
    const {results, isSearching, getCategories} = useStoreSearch({query, maxResults: maxResultsToShow, showCategories});
    const categoryList = showCategories ? getCategories('all') : {isLoading: false, categories: []};
    const widthGrid = 'grid' === layout && showImage ? imageSize : 170;
    const {isLoading, categories} = categoryList;

    const classes = classnames(
        'ywcas-search-results',
        className,
        layout
    );

    if (isLoading || isSearching) {
        return <Spinner color="default" size="md" thickness={3.6}/>;
    }

    const gridStyle = {
        gridTemplateColumns : `repeat(auto-fill, minmax(${parseInt(widthGrid) + 20}px, ${parseInt(widthGrid) + 20}px))!important`,
        marginTop:'30px',
        rowGap:'30px',
        columnGap:'15px',
    }

    return (
        <Box className={classes} sx={ layout === 'grid' ? { '&.wp-block-yith-product-results-block .lapilliUI-Grid__root' : {...gridStyle} } : {}}>

            { !isSearching && results.totalResults > 0  ?
                (
                <ProductResults searchQuery={query} categoryList={categories} results={results} {...props} />
                )
                : (
                    <Typography>{noResults}</Typography>
                )
            }
        </Box>
    );
};

export default Block;
